@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
}

.component__wrapper {
  width: 900px;
  margin: 10px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
}

.header__text {
  font-size: 26px;
}

.update__icon {
  color: #72d86f;
  margin-left: 15px;
  padding: 3px;
  border: 1px solid #000000;
  border-radius: 50%;
  cursor: default;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.update__icon:active {
  background-color: lightgoldenrodyellow;
}

.list__items {
  width: 100%;
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.list__item {
  padding: 30px;
  margin: 0 30px 30px 0;
  height: 300px;
  width: 250px;
  border: 1px solid #000000;
  position: relative;
}

.list__item:last-child {
  margin-right: 0;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
}

.form__label {
  margin: 0 0 10px 10px;
}

.form__ta {
  width: 500px;
  height: 150px;
  padding: 5px;
  outline: 0;
  border: 1px solid #000000;
  resize: none;
  overflow: hidden;
}

.form__button {
  padding: 5px;
  outline: 0;
  border: 1px solid #000000;
  border-radius: 50%;
  background-color: #ffffff;
  position: absolute;
  bottom: 10px;
  right: 15px;
}

.form__button:active {
  background-color: lightgoldenrodyellow;
}

.note__content {
  overflow: auto;
  height: 100%;
  overflow-wrap: break-word;
}

.note__delete {
  position: absolute;
  cursor: default;
  padding: 3px;
  border: 1px solid #000000;
  border-radius: 50%;
  color: red;
  top: -17px;
  right: -17px;
  background-color: #ffffff;
}

.note__delete:active {
  background-color: lightgoldenrodyellow;
}

